*{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 20px;
}
body{
    padding: 60px;
    background-color: #21232A;
    color: white;
}

input{
    color: gray;
    font-size: 1.2rem;
    width: 100%;
}

.card-grid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.card {
    align-content: center;
    border-radius: 6px;
    border: 1px solid grey;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
    
}

.card p{
    padding: 5px;
    text-align: center;
}

.card img {
    max-height: 170px;
    
}